
@charset "UTF-8";
@import url(./common/swiper.css);
html, body, input, button, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}
:focus {
  outline: 0;
}
ins {
  text-decoration: none;
}
del {
  text-decoration: line-through;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a img {
  border: none;
  text-decoration: none;
}
.clear:after {
  content: "";
  display: block;
  height: 0;
  visibility: hidden;
  clear: both;
}
button {
  cursor: pointer;
}
img {
  display: block;
}
.borderNo {
  border: none !important;
}
.margin0 {
  margin: 0 !important;
}
.pull-left {
  float: left !important;
}
.pull-right {
  float: right !important;
}
.text-right {
  text-align: right;
}
.text-center {
  text-align: center;
}
.marBtw {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
* {
  box-sizing: border-box;
}
body {
  font: 12px/1.5 "微软雅黑";
  color: #fff;
}
.title {
  width: 94%;
  font-size: 24px;
  text-align: center;
  margin-top: 70px;
  text-indent: 3em;
}
header {
  width: 100vw;
  height: 60px;
  position: fixed;
  z-index: 1000;
  top: 0;
  padding: 10px 3% 0;
}
header img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
#app {
  height: 100vh;
  overflow: hidden;
}
#pages {
  height: 192px;
  margin-top: -96px;
  position: fixed;
  top: 50%;
  right: 20px;
  z-index: 1000;
}
#pages li {
    width: 32px;
    height: 32px;
    position: relative;
    border: 1px solid transparent;
    cursor: pointer;
}
#pages li div {
      width: 8px;
      height: 8px;
      border-top: 1px solid transparent;
      background: rgba(0, 0, 0, 0.6);
      background-size: cover;
      border-radius: 50%;
      margin: 11px auto 0;
      transition: .3s;
      transform-origin: 50% 50%;
}
#pages li div.active {
        transform: scale(4);
}
#pages li div img {
        width: 6px;
        height: 6px;
        margin: auto;
}
#pages li span {
      width: 50px;
      line-height: 20px;
      text-align: center;
      position: absolute;
      top: 5px;
      transform: translateX(-170%);
      transition: .3s;
      background-color: rgba(0, 0, 0, 0.6);
      opacity: 0;
      padding: 5px;
      border-radius: 6px;
}
#pages li span::after {
        content: "";
        position: absolute;
        top: 5px;
        right: -12px;
        border-left: 12px solid rgba(0, 0, 0, 0.6);
        border-top: 10px solid transparent;
        border-bottom: 10px solid transparent;
}
#pages li span.active {
        opacity: 1;
}
.app-silde {
  width: 100vw;
  height: 100vh;
  padding: 0 3%;
  position: relative;
}
.app-silde:nth-of-type(1) {
    background: url(/asset/img/bg.png) #87b0a5;
}
.app-silde:nth-of-type(2) {
    background: url(/asset/img/bg.png) #109085;
}
.app-silde:nth-of-type(3) {
    background: url(/asset/img/bg.png) #4d5e8f;
}
.app-silde:nth-of-type(4) {
    background: url(/asset/img/bg.png) #945c4c;
}
.app-silde:nth-of-type(5) {
    background: url(/asset/img/bg.png) #4b85a0;
}
.app-silde:nth-of-type(6) {
    background: url(/asset/img/bg.png) #a29971;
}
@keyframes move {
from {
    opacity: .3;
    transform: translateY(-50%);
}
to {
    opacity: 1;
    transform: translateY(50%);
}
}
.arrow {
  position: fixed;
  bottom: 30px;
  height: 20px;
  width: 100%;
  background: url(/asset/img/arrow.svg) no-repeat center;
  background-size: 20px 100%;
  z-index: 10000;
  margin: auto;
  animation: move 2s linear infinite alternate;
}



